.container {
    width: min(100% - 30px, 1080px);
    margin-inline: auto;
    display: grid;
    align-items: center;
    height: 90vh;
}

.star {
    color: #ffffff43;
    cursor: pointer;
    font-size: 55px;
}

.star.yellow {
    color: #ffd700;
}

.text {
    color: #fff;
    font-size: 30px;
    place-self: center;
}

.ratingSection {
    display: grid;
    justify-content: center;
}

/* ------dialog--------- */

.dialogBtn {
    background-color: #22477a;
    color: #fff;
    width: 110px;
    height: 30px;
    border-radius: 50px;
    font-weight: 600;
    position: absolute;
    right: 0;
    margin: 20px;
}

.dialogBtn:hover {
    background-color: #22477a87;
}

.dialogBox {
    width: 300px;
    height: 200px;
    background-color: #fff;
    display: grid;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 200px;
    left: 600px;
}

.closeIcon {
    color: #22477a;
    font-size: 70px;
    place-self: center;
}

.closeDialog {
    width: 120px;
    height: 30px;
    border-radius: 50px;
    background-color: #22477a;
    color: #fff;
    font-weight: 600;
}

.closeDialog:hover {
    background-color: #22477abb;
}